export default {
	data() {
		return {
			activeKey: 0,
			checked: false,
			modalName: null,
			list: [],
			arr: [],
			price: 15.00,
			value: 0,
			num: 0,
			numb: 0,
			prices: 0,
			plusList:{}
		}
	},
	onLoad() {
		this.leftList()
		this.mym(2)
	},
	methods: {
		onChange(event) {
			console.log(event)
			this.checked = event.detail
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		leftList() {
			var _this = this;
			uni.request({
				url: "https://wxmall.xingyuncm.cn/api/Category/index",
				success(res) {
					console.log(res)
					_this.list = res.data.list;
				}
			})
		},
		mym(id) {
			var _this = this;
			uni.request({
				url: "https://wxmall.xingyuncm.cn/api/Category/getcat",
				data: {
					cat_id: id
				},
				success(res) {
					console.log(res);
					_this.arr = res.data.catList;
				}
			})
		},
		onChange1(e) {
			let _this = this
			console.log(e.detail)
			_this.num = e.detail
			_this.prices = _this.num * _this.price
		},
		onPlus(e) {
			console.log(e)
			this.plusList = e
		},
		clickNavi(){
			let _this = this;
			let price = _this.price; //单价
			let prices = _this.prices; //总价
			let name = _this.plusList.name; //商品名字
			let bz_1 = _this.plusList.bz_1; //商品图片
			let num = _this.num //商品数量
			uni.navigateTo({
				url:"../index/submit?price=" + price + "&prices=" + prices + "&name=" + name + "&bz_1=" + bz_1 + "&num=" + num
			})
		}
	}
}
